"use client"

import { useEffect, useState } from "react"
import { useRouter } from "next/navigation"
import { Button } from "@/components/ui/button"
import { Card } from "@/components/ui/card"
import { FeedbackModal } from "@/components/modals/feedback-modal"
import { authService } from "@/lib/auth-service"
import { useAuth } from "@/hooks/use-auth"
import { LogOut, Users, FileText, TrendingUp, Calendar, AlertCircle, Home, ExternalLink } from "lucide-react"

export default function DashboardPage() {
  const router = useRouter()
  const { logout } = useAuth()
  const [userRole, setUserRole] = useState<"leader" | "poc" | null>(null)
  const [loading, setLoading] = useState(true)
  const [feedback, setFeedback] = useState<{ type: "success" | "error"; title: string; message: string } | null>(null)

  useEffect(() => {
    const accessToken = authService.getAccessToken()
    if (!accessToken) {
      router.push("/login")
      return
    }

    try {
      const parts = accessToken.split(".")
      if (parts.length === 3) {
        const decoded = JSON.parse(atob(parts[1]))
        if (decoded.is_poc) {
          setUserRole("poc")
        } else {
          setUserRole("leader")
        }
      }
    } catch (err) {
      console.error("Error decoding token:", err)
    }

    setLoading(false)
  }, [router])

  const handleLogout = async () => {
    await logout()
    setFeedback({ type: "success", title: "Logged Out", message: "You have been logged out successfully" })
    setTimeout(() => router.push("/login"), 1000)
  }

  if (loading) {
    return (
      <div className="min-h-screen bg-background flex items-center justify-center">
        <div className="text-center">
          <div className="w-12 h-12 border-4 border-accent border-t-primary rounded-full animate-spin mx-auto mb-4"></div>
          <p className="text-muted-foreground font-medium">Loading dashboard...</p>
        </div>
      </div>
    )
  }

  return (
    <div className="min-h-screen bg-background">
      <header className="sticky top-0 z-50 bg-card border-b border-border shadow-sm">
        <div className="max-w-7xl mx-auto px-4 sm:px-6 lg:px-8 py-4 flex items-center justify-between gap-4 flex-wrap">
          <div className="flex items-center gap-3 flex-wrap">
            <img src="/images/gitam-logo-w.png" alt="GITAM" className="h-10 object-contain" />
            <div className="border-l border-border pl-3">
              <h1 className="text-lg font-bold sih-heading">SIH Portal</h1>
              <p className="text-xs text-muted-foreground">{userRole === "leader" ? "Team Leader" : "POC"} Dashboard</p>
            </div>
          </div>
          <div className="flex items-center gap-3">
            <Button
              onClick={() => router.push("/")}
              variant="ghost"
              size="sm"
              className="text-muted-foreground hover:text-foreground"
            >
              <Home className="w-4 h-4" />
            </Button>
            <Button onClick={handleLogout} className="button-secondary text-sm" size="sm">
              <LogOut className="w-4 h-4 mr-2" />
              Logout
            </Button>
          </div>
        </div>
      </header>

      {/* Main Content */}
      <main className="max-w-7xl mx-auto px-4 sm:px-6 lg:px-8 py-12">
        {userRole === "leader" && <TeamLeaderDashboard />}
        {userRole === "poc" && <POCDashboard />}
        {!userRole && (
          <Card className="p-8 text-center">
            <AlertCircle className="w-12 h-12 mx-auto text-destructive mb-4" />
            <p className="text-foreground font-semibold mb-4">Unable to determine user role</p>
            <Button onClick={handleLogout}>Return to Login</Button>
          </Card>
        )}
      </main>

      {/* Feedback Modal */}
      <FeedbackModal
        open={!!feedback}
        type={feedback?.type || "info"}
        title={feedback?.title || ""}
        message={feedback?.message || ""}
        onClose={() => setFeedback(null)}
        autoClose={false}
      />
    </div>
  )
}

